'use client'

import { ExternalLink } from 'lucide-react'

interface CardProps {
    title: string
    description: string
    url: string
    isContact?: boolean
}

export default function Card({ title, description, url, isContact }: CardProps) {
    // Random gradient generation based on title length or something deterministic
    const gradients = [
        'from-pink-500 to-rose-500',
        'from-indigo-500 to-blue-500',
        'from-purple-500 to-violet-500',
        'from-teal-500 to-emerald-500',
        'from-orange-500 to-amber-500'
    ];
    // Simple hash to pick a gradient
    const hash = title.split('').reduce((acc, char) => acc + char.charCodeAt(0), 0);
    const gradient = gradients[hash % gradients.length];

    // Initials
    const initials = title.split(' ').map(n => n[0]).join('').substring(0, 2).toUpperCase();

    return (
        <a
            href={url}
            target={isContact ? "_self" : "_blank"}
            rel={isContact ? "" : "noopener noreferrer"}
            className="glass-panel block rounded-2xl p-4 break-inside-avoid hover:-translate-y-2 transition-transform duration-300 group flex flex-col h-full"
        >
            {/* Visual Header / 'Image' Area */}
            <div className={`relative overflow-hidden rounded-xl mb-4 h-32 bg-gradient-to-br ${gradient} flex items-center justify-center shadow-lg group-hover:scale-[1.03] transition-transform duration-500`}>
                <span className="font-serif text-4xl text-white/90 font-bold tracking-widest drop-shadow-sm">
                    {initials}
                </span>
                <div className="absolute inset-0 bg-black/10 group-hover:bg-transparent transition-colors"></div>
            </div>

            <div className="flex flex-col gap-3 flex-1">
                <div className="flex justify-between items-start">
                    <h3 className="font-bold text-white text-lg tracking-wide group-hover:text-primary transition-colors">{title}</h3>
                    <ExternalLink size={16} className="text-white/50 group-hover:text-white transition-colors mt-1" />
                </div>

                <p className="font-sans text-slate-300 text-sm leading-relaxed line-clamp-3">
                    {description}
                </p>

                <div className="mt-auto pt-4 flex items-center gap-2 border-t border-white/5">
                    <div className="h-1.5 w-1.5 rounded-full bg-emerald-400 shadow-[0_0_8px_rgba(52,211,153,0.6)]"></div>
                    <span className="text-xs text-slate-400 font-medium tracking-wide uppercase">
                        {isContact ? 'Contact Me' : 'Live Project'}
                    </span>
                </div>
            </div>
        </a>
    )
}
